@import url(https://fonts.googleapis.com/css?family=Montserrat|Roboto+Mono|Roboto+Slab|Russo+One);
@import url(https://fonts.googleapis.com/css?family=Karla|Lato|Noto+Sans|Noto+Serif|Open+Sans|Roboto+Mono|Ubuntu);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

	*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
}
body{
	background-color: #EEE;
}
.main{
  display:flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  
}
.box{
  height: 500px;
  width: 950px;
  box-shadow:
	-2px -2px 6px 2px #FFF,
  2px 2px 6px 2px #C4C4C4;
  border-radius: 10px;
}
.modalbox{
  height: 500px;
  width: 650px;
  background-color: white;
  -webkit-animation: animateModal 1s;
          animation: animateModal 1s;
}
@-webkit-keyframes animateModal {
  from {
    opacity: 0;
    transform: scale(0);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes animateModal {
  from {
    opacity: 0;
    transform: scale(0);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
.btn1{
  background: none;
  border: none;
}
.btn1:focus{
  outline: none;
}

